<template>
  <div id="app">
    <Header @change="onChangeComponent" />
    <div class="px-5" id="content">
      <HelloWorld />
      <About />
      <Skill />
      <Contact />
      <Footer />
    </div>
  </div>
</template>

<script>
import HelloWorld from './components/HelloWorld.vue'
import Header from './components/Header.vue'
import Footer from './components/Footer.vue'
import About from './components/About.vue'
import Skill from './components/Skills.vue'
import Contact from './components/Contact.vue'
import Vue from 'vue';
import transitions from './utils/transition'

Vue.mixin(transitions)

export default {
  name: 'App',
  components: {
    HelloWorld,
    Header,
    About,
    Skill,
    Contact,
    Footer
  },
  data() {
    return {
      isLoading: false,
      componentId: 'HelloWorld'
    }
  },
  mounted() {
    setTimeout(() => {
      document.querySelectorAll('button').forEach((item) => {
        item.addEventListener('mouseup', (e) => {
          if (e && e.target) {
            e.target.blur();
          }
        });
      });
    });
  },
  methods: {
    onChangeComponent(component) {
      this.$gsap.to(window, {
        scrollTo: '#' + component,
        duration: 0.3,
      });
    }
  }
}
</script>