import { TweenMax, Back } from 'gsap';

export default {
  methods: {
    beforeEnter (el) {
      TweenMax.set(el, {
        transform: 'translate(50px, 0)',
      })
    },
    enter (el, done) {
      TweenMax.to(el, 1, {
        transform: 'translate(0, 0)',
        ease: Back.easeOut
      })
      done()
    },
    leave (el, done) {
      TweenMax.to(el, 1, {
        transformOrigin: '50% 50%',
        ease: Back.easeIn
      })
      done()
    }
  },
}
