<template>
  <header id="header">
    <div class="container">
      <div class="d-flex align-items-center justify-content-between">
        <div class="d-flex align-items-center justify-content-between w-100">
          <div class="logo">
            <h2 class="mb-0">
              <span>J</span>
            </h2>
          </div>
          <nav>
            <ul class="list-inline mb-0 main-nav font-mono">
              <li :class="isActive('about')" @click.prevent="change('about')"><a href="#">About</a></li>
              <li :class="isActive('experience')" @click.prevent="change('experience')"><a href="#">Experience</a></li>
              <li :class="isActive('Skill')" @click.prevent="change('Skill')"><a href="#">Work</a></li>
              <li :class="isActive('contact')" @click.prevent="change('contact')"><a href="#">Contact</a></li>
              <li class="mr-0">
                <button class="btn-lg px-4">Resume</button>
              </li>
            </ul>
          </nav>
          <div class="ham-box" @click="openMenu">
            <div class="ham-box-inner" :class="{'active': isOpenMenu}"></div>
          </div>
        </div>
      </div>
    </div>
    <aside :class="{'active': isOpenMenu}">
      <ol class="font-mono">
        <li :class="isActive('about')" @click="changeSP('about')"><a href="#">About</a></li>
        <li :class="isActive('experience')" @click="changeSP('experience')"><a href="#">Experience</a></li>
        <li :class="isActive('Skill')" @click="changeSP('Skill')"><a href="#">Work</a></li>
        <li :class="isActive('contact')" @click="changeSP('contact')"><a href="#">Contact</a></li>
      </ol>
      <a href="/resume.pdf" class="font-mono btn-lg resume px-4">Resume</a>
    </aside>
  </header>
</template>

<script>
export default {
  name: 'Header',
  data() {
    return {
      active: '',
      isOpenMenu: false,
    }
  },
  mounted() {
    const doc = document.documentElement;
    const w = window;
    let prevScroll = w.scrollY || doc.scrollTop;
    let curScroll;
    let direction = 0;
    let prevDirection = 0;
    const header = document.getElementById('header');
    const checkScroll = function() {

    curScroll = w.scrollY || doc.scrollTop;

    if (curScroll === 0) {
      header.classList.remove('sticky');
    }

    if (curScroll > prevScroll) { 
      direction = 2;
    }
    else if (curScroll < prevScroll) { 
      direction = 1;
    }

    if (direction !== prevDirection) {
      toggleHeader(direction, curScroll);
    }

    prevScroll = curScroll;
  };

  const toggleHeader = function(direction, curScroll) {
    if (direction === 2 && curScroll > 70) { 

      header.classList.add('hide');
      prevDirection = direction;
    }
    else if (direction === 1) {
      header.classList.remove('hide');
      header.classList.add('sticky');
      prevDirection = direction;
    }
  };

  window.addEventListener('scroll', checkScroll);

  },
  methods: {
    isActive(component) {
      return this.active === component ? 'active': ''
    },
    change(component) {
      this.active = component;
      this.$emit('change', component)
    },
    changeSP(component) {
      this.openMenu();
      this.change(component);
    },
    openMenu() {
      const el = document.querySelector('body');
      el.classList.toggle('blur');
      this.isOpenMenu = !this.isOpenMenu;
    }
  }
}
</script>
